	retira_acentos(string) {
		return string.normalize('NFD').replace(/[\u0300-\u036f]/g, '');
	}

	getTime() {
		var date = new Date();

		var seconds = String(date.getSeconds()).padStart(2, '0');
		var minutes = String(date.getMinutes()).padStart(2, '0');
		var hour = String(date.getHours()).padStart(2, '0');

		return `${hour}:${minutes}:${seconds}`;
	}
	getTimestampNow() {
		var date = new Date();

		var dd = String(date.getDate()).padStart(2, '0');
		var mm = String(date.getMonth() + 1).padStart(2, '0'); //January is 0!
		var yyyy = date.getFullYear();

		var seconds = String(date.getSeconds()).padStart(2, '0');
		var minutes = String(date.getMinutes()).padStart(2, '0');
		var hour = String(date.getHours()).padStart(2, '0');

		return `${yyyy}-${mm}-${dd} ${hour}:${minutes}:${seconds}`;
	}

	formBuilderValidatorsEmail() {
		return /^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i;
	}
	validaCPFCNPJ(cpfcnpj) {
		var cpfcnpjJustNumbers = cpfcnpj.replace(/\D/g, '');

		if (cpfcnpjJustNumbers.length == 11) {
			var cpf = cpfcnpjJustNumbers;
			if (
				!cpf ||
				cpf.length != 11 ||
				cpf == '00000000000' ||
				cpf == '11111111111' ||
				cpf == '22222222222' ||
				cpf == '33333333333' ||
				cpf == '44444444444' ||
				cpf == '55555555555' ||
				cpf == '66666666666' ||
				cpf == '77777777777' ||
				cpf == '88888888888' ||
				cpf == '99999999999'
			)
				return false;
			var soma = 0;
			var resto;
			for (var i = 1; i <= 9; i++)
				soma = soma + parseInt(cpf.substring(i - 1, i)) * (11 - i);
			resto = (soma * 10) % 11;
			if (resto == 10 || resto == 11) resto = 0;
			if (resto != parseInt(cpf.substring(9, 10))) return false;
			soma = 0;
			for (var i = 1; i <= 10; i++)
				soma = soma + parseInt(cpf.substring(i - 1, i)) * (12 - i);
			resto = (soma * 10) % 11;
			if (resto == 10 || resto == 11) resto = 0;
			if (resto != parseInt(cpf.substring(10, 11))) return false;
			return true;
		} else if (cpfcnpjJustNumbers.length == 14) {
			var cnpj = cpfcnpjJustNumbers;
			if (
				!cnpj ||
				cnpj.length != 14 ||
				cnpj == '00000000000000' ||
				cnpj == '11111111111111' ||
				cnpj == '22222222222222' ||
				cnpj == '33333333333333' ||
				cnpj == '44444444444444' ||
				cnpj == '55555555555555' ||
				cnpj == '66666666666666' ||
				cnpj == '77777777777777' ||
				cnpj == '88888888888888' ||
				cnpj == '99999999999999'
			)
				return false;
			var tamanho = cnpj.length - 2;
			var numeros = cnpj.substring(0, tamanho);
			var digitos = cnpj.substring(tamanho);
			var soma = 0;
			var pos = tamanho - 7;
			for (var i = tamanho; i >= 1; i--) {
				soma += numeros.charAt(tamanho - i) * pos--;
				if (pos < 2) pos = 9;
			}
			var resultado = soma % 11 < 2 ? 0 : 11 - (soma % 11);
			if (resultado != digitos.charAt(0)) return false;
			tamanho = tamanho + 1;
			numeros = cnpj.substring(0, tamanho);
			soma = 0;
			pos = tamanho - 7;
			for (var i = tamanho; i >= 1; i--) {
				soma += numeros.charAt(tamanho - i) * pos--;
				if (pos < 2) pos = 9;
			}
			resultado = soma % 11 < 2 ? 0 : 11 - (soma % 11);
			if (resultado != digitos.charAt(1)) return false;
			return true;
		}
	}

	extensoesProibidas(event) {
		let extensao = this.getExtensionOfFile(event.target.files[0].name);
		var deny = [
			'html',
			'exe',
			'js',
			'py',
			'php',
			'cc',
			'cpp',
			'cxx',
			'c',
			'c++',
			'h',
			'hh',
			'hpp',
			'hxx',
			'h++',
			'c++',
			'jar',
			'java',
			'deb',
			'tar',
			'zip',
		];
		return deny.indexOf(extensao.toLowerCase()) > -1;
	}
	extensoesValidasAnexos(event) {
		let extensao = this.getExtensionOfFile(event.target.files[0].name);
		var days = [
			'pdf',
			'png',
			'jpg',
			'jpeg',
			,
			'doc',
			'docs',
			'docx',
			'xlsx',
			'xls',
		];
		return days.indexOf(extensao.toLowerCase()) > -1;
	}
	getExtensionOfFile(name) {
		return name.split('.').pop();
	}

	onFileChangedAll(event, type = 'image') {
		let findFile = false;

		if (event.target.files[0] == undefined) {
			return false;
		}
		// console.log(event.target.files);
		var selectedFile: any[] = [];
		var urlImg: any[] = [];
		for (let file of event.target.files) {
			for (let ext of this.typeFile[type]) {
				if (file.type.includes(ext)) {
					findFile = true;
					let tmppath = URL.createObjectURL(file);
					urlImg.push(this.sanitizer.bypassSecurityTrustUrl(tmppath));
					selectedFile.push(file);
				}
			}
		}
		if (!findFile) {
			return false;
		}
		// console.log(selectedFile);

		if (selectedFile.length == 1) {
			const selectF = selectedFile.shift();
			const urlI = urlImg.shift();
			return { selectedFile: selectF, urlImg: urlI };
		}
		return { selectedFile, urlImg };
	}
	onFileChanged(event) {
		if (this.extensoesProibidas(event)) {
			this.notificationService.notifySweet('Arquivo Proibido');
			return false;
		}
		var tmppath = URL.createObjectURL(event.target.files[0]);
		let extensao = this.getExtensionOfFile(event.target.files[0].name);
		let img;
		switch (extensao) {
			case 'png':
			case 'jpg':
			case 'jpeg':
				img = tmppath;
				break;
			default:
				img = '/assets/img/file/' + extensao + '.svg';
				break;
		}
		let selectedFile = event.target.files[0];
		let dados: object = { img: img, selectedFile: selectedFile };

		return dados;
	}
	onFileSet(no_documento) {
		let extensao = this.getExtensionOfFile(no_documento);
		let img;
		switch (extensao) {
			case 'png':
			case 'jpg':
			case 'jpeg':
				img = no_documento;
				break;
			default:
				img = '/assets/img/file/' + extensao + '.svg';
				break;
		}
		return img;
	}

	formatarDataParaCompararNoBanco(data) {
		if (data) {
			let dia = data.getDate().toString();
			let diaF = dia.length == 1 ? '0' + dia : dia;
			let mes = (data.getMonth() + 1).toString(); //+1 pois no getMonth Janeiro comea com zero.
			let mesF = mes.length == 1 ? '0' + mes : mes;
			let anoF = data.getFullYear();
			return anoF + '-' + mesF + '-' + diaF;
		}
	}

